@use "global" as *;


.page-about__gallery {
    position: relative;
}

.page-about__gallery::before {
    background-image: url(../../assets/images/common/fish-image.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    content: "";
    height: rem(50);
    position: absolute;
    height: rem(51);
    right: rem(14);
    display: none;
    width: rem(109);
    display: none;

    @include mq("md") {
        display: block;
        height: rem(150);
        right: rem(46);
        top: rem(-73);
        width: rem(301);
        right: calc(50% - min(48.5vw, rem(639)));
    }
}

.page-about__gallery-inner {}

.page-about__gallery-title {}