@use "global" as *;

.card2 {
  box-shadow: 0 0 rem(8) gray;
  margin: 0 auto;
  padding: rem(14) rem(17) rem(20) rem(15);
  margin-top: rem(0);
  max-width: 100%;

}

.card2:nth-child(n + 2) {
  margin-top: rem(22);

  @include mq("md") {
    margin-left: rem(0);
    margin-top: rem(0);
  }
}

.card2__head {
  display: flex;
}

.card2__detail {
  border-bottom: rem(1) solid $green;
  margin-top: rem(0);

  @include mq("md") {
    margin-top: rem(35);
    width: 58%;
  }
}

.card2__category {
  display: block;

  @include mq("md") {
    display: flex;
    justify-content: space-between;
    align-items: end;
  }
}

.card2__age {
  font-size: rem(12);
}

.card2__course {
  font-size: rem(12);
  border: rem(1) solid $green;
  color: $green;
  display: inline-block;
  padding: rem(6) rem(12) rem(4) rem(10);

  @include mq("md") {
    padding: rem(6) rem(10) rem(8) rem(20);

  }
}

.card2__title {
  font-size: rem(16);
  font-weight: $semibold;
  margin-top: rem(4);
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;

  @include mq("md") {
    font-size: rem(24);
    font-weight: 500;
    margin-top: rem(7);
    letter-spacing: 0.05em;

  }
}

.card2__img {
  max-width: rem(151);

  @include mq("md") {
    max-width: rem(180);
  }
}

.card2__text {
  margin-top: rem(10);
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 10;
  overflow: hidden;

  @include mq("md") {
    margin-top: rem(42);
    -webkit-line-clamp: 7;
  }
}

.card2__text--position {

  @include mq("md") {
    margin-top: rem(41);
    -webkit-line-clamp: 7;

  }
}

div.card2__img {
  position: relative;
  width: 100%;
  margin: rem(0) auto rem(0);
  margin-top: rem(10);
  height: rem(120);

  @include mq("md") {
    height: rem(150);
    position: relative;
    margin: rem(0) auto rem(0);
  }
}

.card2__img {
  padding-top: 0%; //比率の計算式 横233/縦350*100%=画像の比率
}

.card2__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  padding-left: rem(16);
}

div.card2__img div.color {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: $green;
}

div.card2__img img {
  display: block;
  width: 100%;
}