/*ボックス枠のCSS*/
.label_test:after {
  border: 0.0625rem solid #408F95;
  border-radius: 0%;
  content: "";
  display: block;
  height: 0.875rem;
  left: 0.9375rem;
  margin: -0.5rem 0 0 -0.125rem;
  position: absolute;
  top: 50%;
  width: 0.875rem;
}

.label_test {
  position: relative;
  padding: 0 0 0 1.875rem;
  width: 104%;
}

.label_test:after, .label_test:before {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
}

/*チェックマークの追加*/
.label_test:before {
  border-bottom: 0.125rem solid red;
  border-right: 0.125rem solid red;
  height: 0.5625rem;
  left: 1.125rem;
  margin-top: -0.4375rem;
  opacity: 0;
  transform: rotate(45deg);
  width: 0.3125rem;
}

/*チェックボックスがチェックされた時に動作する内容*/
input[type=checkbox]:checked + .label_test:before {
  opacity: 1;
}

