@use "global" as *;

.button2 {
  
}

.button2__items {
  max-width:  rem(745);
font-size: rem(14);
display: flex;
justify-content: center;
  @include mq("md") {
 max-width:  rem(744);
 justify-content: start;
  }
}

.button2__item {
  display: inline-block;
  width: 100%; //横幅
  text-align: center; //文字を左に寄せる
  position: relative;
  z-index: 1;
  background-color: $white; //背景色
  padding-top: rem(4); //ボタン内範囲
  padding-left: rem(12); //ボタン内範囲
  padding-right: rem(12); //ボタン内範囲
  padding-bottom: rem(4); //ボタン内範囲
  border: rem(1) solid $green; //ボタン縁色
  transition: background-color .3s;
}

.button2__item--green {
  background-color: $green; //背景色
}

.button2__item--white {
  background-color: $white; //背景色
}

.button2__item-link {
  font-size: rem(14); ///文字サイズ
  line-height: 1.5714; //文字の高さ
  color: $green; //文字の色
  font-family: $base-font-family;
  font-weight: $normal; //文字太さ
  transition: color .3s; //文字色の変化速度

}

.button2__item-link--white {
  color: $white; //文字の色
}

.button2__item-link--green {
  color: $green; //文字の色

}

.button2__item::before {
  //.buttonの前方
  background: $green; //アニメーション時の色
  position: absolute; //親要素の左上にピッタリとつける
  top: 0; //親要素の左上にピッタリとつける//上方向
  left: 0; //親要素の左上にピッタリとつける//左方向
  content: ''; //擬似要素
  width: 100%; //水平(X軸)方向の大きさがそのままの大きさになる
  height: 100%; //水平(X軸)方向の大きさがそのままの大きさになる
  transform: scale(0, 1); //水平(X軸)方向の大きさがそのままの大きさになるwith,height,transform: scale(0, 1);はセット
  transform-origin: left top; //背景が変わる向き
  transition: .2s cubic-bezier(0.45, 0, 0.55, 1); //transition：変化速度 cubic-bezier:変化速度に緩急をつける
  z-index: -1;
}

.button2__item--white::before {
  background-color: $white; //背景色
}

.button2__item:hover .button2__item-link {
  //アニメーション・左文字色を変える
  color: $white; //左文字色を変える
}

.button2__item--white:hover .button2__item-link--green {
  color: $green; //左文字色を変える
}

.button2__item:hover::before {
  transform: scale(1, 1); //buttonのアニメーションをX軸方向にそのままのサイズに変化させる
}

.button2__item--white {
  background-color: $white; //背景色
}

.button2__item--white::before {
  background-color: $white; //背景色
}

.button2__item--white:hover .button2__item-link--green {
  color: $green; //左文字色を変える
}